










import Vue from 'vue'
import Main from './components/Main.vue'
import Snow from './components/Snow.vue'
import BGContainer from './components/BGContainer.vue'
import Component from 'vue-class-component'

@Component({
  components: {
    Main,
    Snow,
    BGContainer
  }
})
export default class App extends Vue{
  constructor () {
    super()
  }

  mounted () {
    // 绑定键盘按键
    document.onkeydown = (e) => {
      switch (this.$store.state.mode) {
        case 0: {
          // 观赏状态
          // Esc退出到0
          // Enter进入1
          // 空格进入2
          if (e.keyCode == 27) {
            this.$store.commit('changeMode', { mode: 0 })
          }
          if (e.keyCode == 13) {
            this.$store.commit('changeMode', { mode: 1 })
          }
          if (e.keyCode == 32) {
            this.$store.commit('changeMode', { mode: 2 })
          }
          break
        }
        case 1: {
          // 输入状态
          // Esc退出到0
          // Enter应该跳转搜索
          // 空格不应该设置功能（要输入内容的）
          if (e.keyCode == 27) {
            this.$store.commit('changeMode', { mode: 0 })
          }
          if (e.keyCode == 13) {
            // 跳转搜索
            this.$store.commit('doSearch')
          }
          break
        }
        case 2: {
          // 功能状态还没想好
          if (e.keyCode == 27) {
            this.$store.commit('changeMode', { mode: 0 })
          }
          if (e.keyCode == 13) {
            this.$store.commit('changeMode', { mode: 1 })
          }
          if (e.keyCode == 32) {
            this.$store.commit('changeMode', { mode: 2 })
          }
          break
        }
        case 3: {
          // 定制状态
          // Esc退出到0
          if (e.keyCode == 27) {
            this.$store.commit('changeMode', { mode: 0 })
          }
        }
        case 4: {
          // 设置状态
          // Esc退出到0
          if (e.keyCode == 27) {
            this.$store.commit('changeMode', { mode: 0 })
          }
        }
      }
    }
    // 初始化 store 的状态
    this.$store.dispatch('initialization')
  }
}
