




















import Vue from 'vue'
import Component from 'vue-class-component'

@Component
export default class Profile extends Vue {
  constructor () {
    super()
  }

  doLogout () {
    this.$store.commit('logout')
    this.$store.commit('changeMode', { mode: 0 })
    this.$store.commit('displayTips', { msg: '退出成功' })
  }
}
